"use client";
import React, { useState } from "react";
import { useRouter } from "next/navigation";
import { useLoginClient } from "../context/regester/login_context";
import { useApi } from "../context/ApiContext";

export default function AddPortfolioPage() {
    const router = useRouter();
    const { saveData } = useLoginClient();
    const { baseUrl } = useApi();

    const [name, setName] = useState("");
    const [description, setDescription] = useState("");
    const [images, setImages] = useState<FileList | null>(null);
    const [uploading, setUploading] = useState(false);
    const [error, setError] = useState("");
    const [successMessage, setSuccessMessage] = useState("");

    const handleUpload = async () => {
    if (!images || !name.trim() || !description.trim()) {
        setError("‚ùó Ÿäÿ¨ÿ® ÿ™ÿπÿ®ÿ¶ÿ© ÿ¨ŸÖŸäÿπ ÿßŸÑÿ≠ŸÇŸàŸÑ ŸàÿßÿÆÿ™Ÿäÿßÿ± ÿµŸàÿ±.");
        setSuccessMessage("");
        return;
    }

    setUploading(true);
    setError("");
    setSuccessMessage("");

    const formData = new FormData();
    for (let i = 0; i < images.length; i++) {
        formData.append("ImageFiles", images[i]);
    }

    try {
        const url = `${baseUrl}/api/Portfolio?Name=${encodeURIComponent(
        name
        )}&Description=${encodeURIComponent(description)}`;

        const res = await fetch(url, {
        method: "POST",
        body: formData,
        headers: {
            Accept: "application/json",
            Authorization: `Bearer ${saveData?.token}`, // ‚úÖ ÿ£ÿ∂ŸÅŸÜÿß ÿßŸÑÿ™ŸàŸÉŸÜ ŸáŸÜÿß
        },
        });

        const responseText = await res.text();
        // console.log("üîç Status Code:", res.status);
        // console.log("üì® Response Text:", responseText);

        if (!res.ok) throw new Error("ŸÅÿ¥ŸÑ ÿ±ŸÅÿπ ÿßŸÑÿµŸàÿ±.");

        setSuccessMessage("‚úÖ ÿ™ŸÖ ÿ±ŸÅÿπ ÿßŸÑÿ®Ÿàÿ±ÿ™ŸÅŸàŸÑŸäŸà ÿ®ŸÜÿ¨ÿßÿ≠!");
        setError("");
        setTimeout(() => {
        router.push(`/viewprofile/${saveData?.workerId}`);
        }, 1500);
    } catch (err: any) {
        setError(err.message || "‚ùå ÿ≠ÿØÿ´ ÿÆÿ∑ÿ£ ÿ£ÿ´ŸÜÿßÿ° ÿßŸÑÿ±ŸÅÿπ.");
    } finally {
        setUploading(false);
    }
    };

    return (
        <div className="min-h-screen bg-gray-100 py-10 px-6">
        <div className="max-w-xl mx-auto bg-white rounded-lg shadow p-6">
            <h1 className="text-xl font-bold mb-4 text-center text-green-700">
            ÿ•ÿ∂ÿßŸÅÿ© ÿ®Ÿàÿ±ÿ™ŸÅŸàŸÑŸäŸà ÿ¨ÿØŸäÿØ
            </h1>

            {error && <p className="text-red-600 text-sm mb-3">{error}</p>}
            {successMessage && (
            <p className="text-green-600 text-sm mb-3">{successMessage}</p>
            )}

            <div className="mb-4">
            <label className="block mb-1 font-semibold text-sm">
                ÿßÿ≥ŸÖ ÿßŸÑÿ®Ÿàÿ±ÿ™ŸÅŸàŸÑŸäŸà
            </label>
            <input
                type="text"
                value={name}
                onChange={(e) => setName(e.target.value)}
                className="w-full border px-3 py-2 rounded outline-none focus:ring-2 focus:ring-green-400"
                placeholder="ŸÖÿ´ÿßŸÑ: ÿ£ÿπŸÖÿßŸÑ ÿßŸÑÿØŸáÿßŸÜÿßÿ™"
            />
            </div>

            <div className="mb-4">
            <label className="block mb-1 font-semibold text-sm">ŸàÿµŸÅ</label>
            <textarea
                value={description}
                onChange={(e) => setDescription(e.target.value)}
                className="w-full border px-3 py-2 rounded outline-none focus:ring-2 focus:ring-green-400"
                placeholder="ŸàÿµŸÅ ŸÖÿÆÿ™ÿµÿ± ÿπŸÜ ÿßŸÑÿµŸàÿ± ÿ£Ÿà ÿßŸÑŸÖÿ¥ÿ±Ÿàÿπ"
            />
            </div>

            <div className="mb-4">
            <label className="block mb-1 font-semibold text-sm">ÿßŸÑÿµŸàÿ±</label>
            <input
                type="file"
                accept="image/*"
                multiple
                onChange={(e) => setImages(e.target.files)}
            />
            </div>

            {images && (
            <div className="grid grid-cols-3 gap-2 mb-4">
                {Array.from(images).map((img) => (
                    <img
                        key={img.name} // ‚úÖ ÿßÿ≥ÿ™ÿÆÿØŸÖ ÿßÿ≥ŸÖ ÿßŸÑÿµŸàÿ±ÿ© ŸÉŸÄ key ŸÅÿ±ŸäÿØ
                        src={URL.createObjectURL(img)}
                        alt={img.name}
                        className="h-24 w-full object-cover rounded"
                    />
                    ))}

            </div>
            )}

            <button
            onClick={handleUpload}
            disabled={uploading}
            className="w-full bg-green-600 text-white py-2 rounded hover:bg-green-700 transition"
            >
            {uploading ? "ÿ¨ÿßÿ±Ÿä ÿßŸÑÿ±ŸÅÿπ..." : "ÿ±ŸÅÿπ ÿßŸÑÿ®Ÿàÿ±ÿ™ŸÅŸàŸÑŸäŸà"}
            </button>
        </div>
        </div>
    );
}
